import React from 'react'

const Checkbox = ({ label, isSelected, onCheckboxChange }) => (
  <div className='form-check'>
    <label>
      <input
        type='checkbox'
        name={label}
        checked={isSelected}
        onChange={onCheckboxChange}
        className='create-user-input'
      />
      {label}
    </label>
  </div>
)

export default Checkbox
